<template>
     <div id="account">
          <div id="content_card">
               <AccountCard v-if="!edit" />
               <AccountCardEdit v-if="edit"></AccountCardEdit>
               <div id="main">
                    <h1>{{firstName}} {{lastName}}</h1>
                    <span @click="signoutUser">Sign out</span>
                    <span @click="deleteUser">Delete User</span>
               </div>
               <svg id="line">
                    <line
                         class="cls"
                         x1="1.2"
                         y1="0"
                         x2="1.2"
                         y2="1000"
                    />
               </svg>
               <div id="side"></div>
          </div>
     </div>
</template>

<script>
import axios from "axios";
import AccountCard from "../components/AccountCard.vue";
export default {
     data() {
          return {
               edit: false,

               firstName: this.$store.get("firstName"),
               lastName: this.$store.get("lastName"),
               email: this.$store.get("email"),
               username: this.$store.get("username"),
               password: "",
               bio: this.$store.get("bio"),
               pfp: this.$store.get("pfp")
          };
     },
     methods: {
          pullUser() {
               axios({
                    method: "get",
                    url:
                         this.$store.get("apiUrl") +
                         "users/profile/" +
                         this.$store.get("username")
               })
                    .then(response => {
                         if (response.data.bio != null) {
                              this.$store.set("bio", response.data.bio);
                              if (this.$store.get("bio") != this.bio) {
                                   this.bio = response.data.bio;
                              }
                         }
                         if (response.data.firstName != null) {
                              this.$store.set(
                                   "firstName",
                                   response.data.firstName
                              );
                              if (
                                   this.$store.get("firstName") !=
                                   this.firstName
                              ) {
                                   this.firstName = response.data.firstName;
                              }
                         }
                         if (response.data.lastName != null) {
                              this.$store.set(
                                   "lastName",
                                   response.data.lastName
                              );
                              if (
                                   this.$store.get("lastName") != this.lastName
                              ) {
                                   this.lastName = response.data.lastName;
                              }
                         }
                         if (response.data.username != null) {
                              this.$store.set(
                                   "username",
                                   response.data.username
                              );
                              if (
                                   this.$store.get("username") != this.username
                              ) {
                                   this.username = response.data.username;
                              }
                         }
                         if (response.data.imageUrl != null) {
                              this.$store.set("pfp", response.data.imageUrl);
                              if (this.$store.get("pfp") != this.pfp) {
                                   this.pfp = response.data.imageUrl;
                              }
                         }
                    })
                    .catch(function(error) {
                         console.log(error);
                    });
          },
          deleteUser() {
               axios({
                    method: "delete",
                    url: this.$store.get("apiUrl") + "users/delete",
                    headers: {
                         authorization: this.$store.get("token")
                    }
               })
                    .then(response => {
                         this.$store.dispatch("signOut");
                    })
                    .catch(function(error) {
                         console.log(error);
                    });
          },
          updateUser() {
               const userData = {
                    email: this.email,
                    username: this.username,
                    firstName: this.firstName,
                    lastName: this.lastName,
                    password: this.password,
                    bio: this.bio
               };
               const userDataJSON = JSON.stringify(userData);
               axios({
                    method: "patch",
                    url: this.$store.get("apiUrl") + "users/update",
                    data: userDataJSON,
                    headers: {
                         authorization: this.$store.get("token")
                    }
               })
                    .then(response => {
                         this.$store.set("token", response.data.encoded);
                         this.$store.set("email", this.email);
                         this.$store.set("username", this.username);
                         this.$store.set("firstName", this.firstName);
                         this.$store.set("lastName", this.lastName);
                         this.$store.set("bio", this.bio);
                         this.$store.set("pfp", this.pfp);
                         this.collapseToggle();
                    })
                    .catch(function(error) {
                         $("#errors").text(error);
                    });
          },
          signoutUser() {
               this.$store.dispatch("signOut");
          },
          toggleEdit() {
               this.edit = !this.edit;
          }
     },
     mounted() {
          this.pullUser();
     },
     components: {
          AccountCard
     }
};
</script>

<style lang="scss" scoped>
@import "../assets/global.scss";
#account {
     display: grid;
     place-items: center;
     height: 87.3vh;
     #content_card {
          height: 100%;
          display: grid;
          grid-template-rows: 1fr;
          grid-template-columns: 30% 1fr auto 1fr;
          background: white;
          border-radius: 20px;
          width: 1000px;
          height: 600px;

          #profile_card {
               grid-column: 1;
          }
          #main {
               grid-column: 2;
               display: flex;
               flex-flow: column nowrap;
               h1 {
                    position: relative;
                    top: -45px;
               }
               span {
                    cursor: pointer;
                    margin-bottom: 30px;
                    &:hover {
                         font-size: 1.1rem;
                    }
               }
          }
          #line {
               grid-column: 3;
               @extend .clear;
               display: grid;
               place-items: center;
               .cls {
                    fill: none;
                    stroke: #c5c5c5;
                    stroke-width: 1px;
               }
               height: 100%;
               max-width: 2px;
          }
          #side {
               height: 100%;
               grid-column: 4;
          }
     }
}
</style>